.profile-photo {
    position: relative;
    height: 30px;
    width: 30px;
    overflow: hidden;
    border-radius: 50%;

    .initials {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        color: var(--theme-color-historyPeerUserpicFg);
        font-size: 18px;

        &.color_1 { background-color: var(--theme-color-historyPeer1UserpicBg); }
        &.color_2 { background-color: var(--theme-color-historyPeer8UserpicBg); }
        &.color_3 { background-color: var(--theme-color-historyPeer5UserpicBg); }
        &.color_4 { background-color: var(--theme-color-historyPeer2UserpicBg); }
        &.color_5 { background-color: var(--theme-color-historyPeer4UserpicBg); }
        &.color_6 { background-color: var(--theme-color-historyPeer4UserpicBg); }
        &.color_7 { background-color: var(--theme-color-historyPeer6UserpicBg); }
    }

    img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    .svg {
        background-color: var(--theme-color-historyPeerSavedMessagesBg);
        fill: var(--theme-color-historyPeerUserpicFg);
        height: 100%;
        width: 100%;

        svg {
            height: 100%;
            width: 100%;

            path {
                stroke: var(--theme-color-historyPeerUserpicFg);
            }
        }
    }
}
